.header {
  padding-top: 32px;

  &>.container {
    padding: 0 65px;

    @media screen and (max-width:$w3) {
      padding: 0 20px;
    }
  }

  & .main-button {
    margin: 30px auto 0;
    display: block;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: $purple;
    z-index: 0;
  }

  &__img {
    margin: 50px auto 0px;
    text-align: center;

    @media screen and (max-width:$w4) {
      margin: 80px auto 0;
    }

    &>img {
      width: 520px;
      margin: 0 auto;
    }
  }

  &__title {
    text-align: center;
    font-size: 40px;
    color: #ffffff;
    font-weight: normal;

    @media screen and (max-width:$w4) {
      font-size: 20px;
    }
  }
}

.hat {
  display: flex;
  justify-content: space-between;
  align-items: center;


  @media screen and (max-width:$w3) {
    justify-content: flex-end;
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    margin-right: 95px;

    @media screen and (max-width:$w2) {
      margin-right: 0;
    }

    @media screen and (max-width:$w3) {
      display: none;
    }

    &-link {
      color: #ffffff;
      transition: $trs;
      position: relative;

      &:not(:last-child) {
        margin-right: 32px;

        @media screen and (max-width:$w2) {
          margin-right: 20px;
        }
      }

      &:after {
        content: "";
        position: absolute;
        display: block;
        bottom: -2px;
        left: 0;
        height: 1px;
        width: 0;
        transition: $trs;
        background: $goldHover;
      }

      &:hover {
        color: $yellow;

        &:after {
          width: 100%;
        }
      }
    }
  }

  &__lang {
    display: flex;
    align-items: center;
    justify-content: center;

    @media screen and (max-width:$w3) {
      display: none;
    }

    &-item {

      &:not(:last-child) {
        margin-right: 16px;
      }
    }

    &-link {
      cursor: pointer;
      transition: $trs;
      color: #ffffff;
      position: relative;
      background: transparent;

      &:after {
        content: "";
        position: absolute;
        display: block;
        bottom: -5px;
        left: 0;
        height: 2px;
        width: 0;
        transition: $trs;
        background: $yellow;
      }

      &:hover,
      &_active {
        color: $yellow;

        &:after {
          width: 100%;
        }
      }
    }
  }
}

.phone {
  font-size: 24px;
  color: #ffffff;
  background: url('../img/phone.svg') no-repeat center left;
  padding-left: 40px;
  transition: $trs;
  position: relative;

  @media screen and (max-width:$w2) {
    font-size: 20px;
  }

  &:hover {
    color: $yellow;
  }

  @media screen and (max-width: $w2) {
    padding-left: 15px;
  }
}

.about {
  padding: 89px 0;

  @media screen and (max-width:$w3) {
    padding: 30px 0;
  }

  &__inner {
    display: flex;

    @media screen and (max-width:$w3) {
      flex-direction: column;
    }
  }

  &__box {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    @media screen and (max-width:$w3) {
      width: 100%;
    }
  }

  &__text {
    max-width: 600px;

    @media screen and (max-width:$w3) {
      margin-bottom: 20px;
      max-width: 100%;
    }
  }

  &__title {
    margin-bottom: 35px;
  }
}

.services {
  &>.container {
    padding: 0;
  }

  &__title {
    margin-bottom: 73px;

    @media screen and (max-width:$w3) {
      margin-bottom: 30px;
    }
  }
}

.service {
  display: flex;

  &:nth-child(2n) {
    flex-direction: row-reverse
  }

  @media screen and (max-width:$w3) {
    flex-direction: column;

    &:nth-child(2n) {
      flex-direction: column;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__img {
    width: 100%;
  }

  &__box {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    @media screen and (max-width:$w3) {
      width: 100%;
    }

    &:nth-child(2) {
      padding: 0 10px;
    }
  }

  &__title {
    margin-bottom: 30px;

    @media screen and (max-width:$w3) {
      margin: 20px 0;
    }
  }

  &__description,
  &__text {
    max-width: 630px;

    @media screen and (max-width: $w1) {
      max-width: 485px;
    }
  }
}

.advantages {
  padding-top: 93px;
  padding-bottom: 85px;

  @media screen and (max-width:$w3) {
    padding: 30px 0;
  }

  &__title {
    margin-bottom: 85px;

    @media screen and (max-width:$w3) {
      margin-bottom: 30px;
    }
  }
}

.advantage {
  flex-basis: 320px;
  position: relative;
  padding: 0 10px 0;

  @media screen and (max-width:$w3) {
    margin-bottom: 30px;
    padding: 0 10px 0;
  }

  @media screen and (max-width:$w4) {
    padding: 0 0 0 0;
    flex-basis: 100%;
  }

  &__title {
    margin-bottom: 33px;

    @media screen and (max-width:$w4) {
      margin-bottom: 8px;
    }
  }

  &__description {
    text-align: center;
  }

  &__list {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
  }
}

.work {
  padding-top: 71px;
  padding-bottom: 81px;
  border-top: 1px solid #6C1396;
  border-bottom: 1px solid #6C1396;

  @media screen and (max-width:$w3) {
    padding: 30px 0;
  }

  &__title {
    margin-bottom: 86px;

    @media screen and (max-width:$w3) {
      margin-bottom: 20px;
    }
  }

  &__option {
    display: flex;
    align-items: center;
    justify-content: center;

    @media screen and (max-width:$w3) {
      flex-direction: column;
    }

    &:first-of-type {
      margin-bottom: 64px;

      @media screen and (max-width:$w3) {
        margin-bottom: 30px;
      }
    }

    &-text {
      position: relative;
      background: url('../img/arrow-r-long.svg') no-repeat center;
      font-size: 22px;
      padding: 27px 0;
      background-position-x: 10px;
      width: 168px;
      text-align: center;

      @media screen and (max-width:$w3) {
        background: none;
      }
    }
  }

  &__steps {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media screen and (max-width:$w2) {
      flex-wrap: wrap;
      justify-content: center;
      max-width: 75%;
    }

    @media screen and (max-width: $w3) {
      flex-direction: column;
    }
  }

  &__step {
    padding-top: 80px;
    position: relative;
    width: 300px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    font-size: 24px;
    font-weight: 600;

    @media screen and (max-width:$w1) {
      width: 250px;
    }

    @media screen and (max-width:$w2) {
      width: 325px;
    }

    @media screen and (max-width:$w3) {
      width: 300px;
      font-size: 20px;
    }

    @media screen and (max-width: $w4) {
      width: 100%;
    }

    &:before {
      content: '';
      display: block;
      position: absolute;
      left: 50%;
      top: 0;
      width: 62px;
      height: 62px;
      border-radius: 50%;
      transform: translateX(-50%);
      background: $gold;
    }

    &:after {
      content: '';
      position: absolute;
      left: 50%;
      top: 14px;
      width: 36px;
      height: 36px;
      transform: translateX(-50%);
      display: flex;
      align-items: center;
      justify-content: center;
    }

    @each $bg in file,
    contract,
    plane,
    check,
    payment {
      &-#{$bg}:after {
        background: url('../img/#{$bg}.svg') no-repeat center;
      }
    }

    &-item {
      position: relative;

      &:not(:last-child) {
        &:after {
          content: '';
          display: block;
          position: absolute;
          left: 100%;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 56px;
          height: 54px;
          background: url('../img/arrow-r.svg') no-repeat center;
          transition: $trs;
        }

        @media screen and (max-width:$w3) {
          margin-bottom: 80px;

          &:nth-child(1),
          &:nth-child(2) {
            margin-bottom: 80px;
          }

          &:after {
            content: '';
            left: 50%;
            top: calc(100% + 40px);
            transform: translate(-50%, -50%) rotate(90deg);
          }
        }
      }

      @media screen and (max-width:$w2) {

        &:nth-child(1),
        &:nth-child(2) {
          margin-bottom: 30px;
        }

        &:nth-child(2) {
          &:after {
            content: none;
          }
        }
      }

      @media screen and (max-width:$w3) {
        &:nth-child(2) {
          &:after {
            content: '';
          }
        }
      }
    }
  }

  &__buttons {
    margin-top: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    @media screen and (max-width:$w3) {
      margin-top: 40px;
    }
  }

  &__button {
    color: #000000;
    font-size: 20px;
    font-weight: bold;
    font-family: 'Source Sans Pro', sans-serif;
    margin: 10px;
    transition: transform $trs, box-shadow $trs;
  }
}

.find {
  padding-top: 46px;
  height: 100vh;

  @media screen and (max-width:$w3) {
    padding: 30px 0;
    height: auto;
  }

  &__title {
    margin-bottom: 43px;

    @media screen and (max-width:$w3) {
      margin-bottom: 30px;
    }
  }

  &__img {
    margin: 0 auto;
    height: 700px;
    width: auto;
    max-height: calc(100vh - 46px - 45px - 43px - 40px);

    @media screen and (max-width:$w3) {
      max-height: unset;
      width: auto;
      height: auto;
    }
  }
}

.callback {
  padding-top: 32px;
  padding-bottom: 23px;
  background: url('../img/callbackBg.jpg') no-repeat center / cover;
  position: relative;

  @media screen and (max-width:$w3) {
    padding: 20px 0;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: $purple;
    z-index: 0;
  }

  &__text {
    font-size: 24px;
    color: #ffffff;

    @media screen and (max-width:$w3) {
      font-size: 20px;
      margin-bottom: 20px;
      text-align: center;
    }
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-around;

    @media screen and (max-width:$w3) {
      flex-direction: column;
    }
  }
}

.footer {
  padding: 35px 0;
  background: #282828;

  .phone {
    font-size: 18px;
    padding-left: 20px;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-around;

    @media screen and (max-width:$w4) {
      flex-direction: column;
    }
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 50%;

    @media screen and (max-width:$w3) {
      flex-direction: column;

      &>a:last-child {
        margin-top: 30px;
      }
    }

    @media screen and (max-width:$w4) {
      width: 100%;

      &:first-child {
        margin-bottom: 30px;
      }
    }
  }

  &__email {
    color: #ffffff;
    cursor: pointer;
    transition: $trs;
    position: relative;
    padding-left: 42px;
    display: flex;
    align-items: center;

    &:hover {
      color: $yellow;
    }

    &:after {
      content: url('../img/mail.svg');
      width: 25px;
      height: 17px;
      position: absolute;
      left: 0;
    }
  }

  &__logo {
    width: 160px;
    cursor: pointer;
    transition: $trs;

    &:hover {
      transform: scale(1.1);
    }
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, .7s);
  z-index: 5;
  cursor: pointer;
  display: none;
  opacity: 0;
  transition: .5s ease;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
  transition: transform .5s ease, opacity .5s ease;
  z-index: 6;
  background: #fff;
  border-radius: 22px;
  font-family: 'Roboto', sans-serif;
  width: 850px;
  display: none;
  opacity: 0;
  font-size: 14px;
  padding: 30px 40px;

  @media screen and (max-width:$w1) {
    padding: 15px 30px;
  }

  @media screen and (max-width:$w3) {
    overflow-y: scroll;
    border-radius: 10px;
    padding: 10px 20px;
    width: 80vw;
    height: 90vh;
  }

  @media screen and (max-width:$w4) {
    width: 90vw;
  }

  &__close {
    position: absolute;
    top: 7px;
    right: 15px;
    font-size: 30px;
    cursor: pointer;
    transition: $trs;

    &:hover {
      color: $yellow;
    }
  }

  &>.second-title {
    font-size: 30px;
  }

  &__title {
    font-family: 'Roboto', sans-serif;
    font-weight: normal;
    margin-bottom: 30px;

    @media screen and(max-width:$w1) {
      margin-bottom: 20px;
    }
  }

  &__form {

    &-field {
      display: flex;
      align-items: center;
      justify-content: space-between;
      cursor: pointer;

      @media screen and (max-width:$w3) {
        flex-direction: column;
      }

      &:nth-last-child(3) {
        margin-top: 15px;
      }

      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }

    &-name {
      width: 185px;

      &>span {
        color: $yellow;
      }

      @media screen and (max-width:$w3) {
        width: 100%;
        margin-bottom: 5px;
        text-align: center;
      }

      @media screen and (max-width:$w4) {
        text-align: left;
      }

      &_abs {
        position: absolute;
        top: 50%;
        right: calc(100% + 5px);
        transform: translateY(-50%);
        width: auto;
        white-space: nowrap;

        @media screen and (max-width:$w3) {
          position: static;
          display: block;
          margin-bottom: 0;
          transform: none;
        }
      }
    }

    &-input-wrap {
      @media screen and (max-width:$w3) {
        width: 100%;
      }

      &_double {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 512px;
        position: relative;

        &>label {
          position: relative;
        }

        @media screen and (max-width:$w3) {
          width: 100%;
          flex-wrap: wrap;

          &>label {
            width: 50%;

            @media screen and (max-width:$w4) {
              width: 100%;
            }
          }
        }
      }
    }

    &-input,
    &-textarea {
      width: 512px;
      padding: 6.5px 10px;
      background: #EFEFEF;
      transition: border-color $trs, transform $trs;
      border: 1px solid transparent;
      position: relative;
      z-index: 1;

      @media screen and (max-width:$w3) {
        width: 100%;
      }

      &:focus {
        border-color: $yellow;
      }

      &_small {
        width: 222px;

        @media screen and (max-width:$w3) {
          width: 100%;
        }
      }

      &::placeholder {
        color: #B5B5B5;
        opacity: 1;
      }

      &:-ms-input-placeholder {
        color: #B5B5B5;
      }

      &::-ms-input-placeholder {
        color: #B5B5B5;
      }
    }

    &-textarea {
      height: 47px;
      font-family: 'Roboto', sans-serif;
    }

    &-checkbox {
      display: none;

      &-item {
        &:hover {
          .modal__form-name {
            &:before {
              border-color: $yellow;
              color: $yellow;
            }
          }
        }
      }

      &~.modal__form-name {
        &:before {
          content: '';
          background: #EFEFEF;
          width: 20px;
          height: 20px;
          position: absolute;
          top: 50%;
          right: calc(100% + 6px);
          transform: translateY(-50%);
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 20px;
          line-height: 20px;
          transition: $trs;
          border: 1px solid transparent;

          @media screen and (max-width:$w3) {
            right: calc(100% + 5px);
          }
        }
      }

      &:checked~.modal__form-name {
        &:before {
          content: '\2713';
        }
      }

      &-list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;

        @media screen and (max-width:$w3) {
          flex-wrap: wrap;
        }
      }

      &-item {
        padding-left: 44px;
        margin: 5px 8px 5px 0;
        padding-left: 30px;

        &:last-child {
          margin-right: 0;
        }
      }

      &-box {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }

      &-label {
        cursor: pointer;
        position: relative;
      }
    }
  }

  &__button {
    display: block;
    margin: 25px auto 0;

    @media screen and (max-width:$w1) {
      margin: 15px auto 0;
    }
  }
}

.download-button {
  font-size: 18px;
  border-radius: 10px;
  transition: transform $trs, box-shadow $trs;
  border: 2px solid transparent;
  background: $gold;
  padding: 9px 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  border-radius: 10px;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    border-radius: 10px;
    background: #282828;
    transition: background $trs;
  }

  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, .8);
    border-width: 0;
    padding: 11px 16px;

    .download-button__text {
      color: #000;
    }

    &::after {
      background: transparent;
    }
  }

  &:active {
    transform: translateY(0);
    box-shadow: 0 0 0 0 transparent;
  }

  &__text {
    background: -webkit-linear-gradient(20.9deg, #A27730 0%, #EBC55F 27.62%, #FEF7BF 56.35%, $yellow 100%);
    -webkit-background-clip: text;
    color: transparent;
    transition: $trs;
    z-index: 1;
  }
}

.float-buttons {
  position: fixed;
  right: 7px;
  bottom: 12px;
  display: flex;
  flex-direction: column;
  z-index: 3;
  visibility: hidden;

  @media screen and (max-width:$w3) {
    visibility: visible;
  }

  &__button {
    width: 65px;
    height: 65px;
    background: $gold;
    border-radius: 10px;
    transition: $trs;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;

    @media screen and (max-width:$w3) {
      width: 45px;
      height: 45px;
    }

    &:after {
      content: '';
      display: block;
      width: 65px;
      height: 65px;
      background-size: 44px;
      background-repeat: no-repeat;
      background-position: center;

      @media screen and (max-width:$w3) {
        width: 45px;
        height: 45px;
        background-size: 30px;
      }
    }

    @each $bg in file,
    massanger,
    wechat {
      &_#{$bg}:after {
        background-image: url('../img/#{$bg}.svg');
      }
    }

    &_file:after {
      background-size: 31px;

      @media screen and (max-width:$w3) {
        background-size: 25px;
      }
    }

    &:not(:first-child) {
      margin-top: 15px;

      @media screen and (max-width:$w3) {
        margin-top: 8px;
      }
    }

    &:hover {
      transform: scale(1.1);
      box-shadow: 0 0 5px 0 rgba(0, 0, 0, 1);
    }

    &:active {
      transform: scale(1);
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
  }
}

.social-links {
  position: absolute;
  left: 7px;
  bottom: 12px;
  display: flex;
  flex-direction: column;
  z-index: 2;

  .tooltip {
    &:after {
      clip-path: polygon(100% 0%, 100% 100%, 0% 50%);
      right: 100%;
      left: auto;
    }
  }
}

.social-link {
  width: 65px;
  height: 65px;
  border-radius: 10px;
  transition: $trs;
  position: relative;

  @media screen and (max-width:$w3) {
    width: 45px;
    height: 45px;
    background-size: 30px;
  }

  &:hover {
    transform: scale(1.1);
    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 1);
  }

  &:active {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }

  &:not(:first-child) {
    margin-top: 8px;
  }

  &:after {
    content: '';
    display: block;
    width: 65px;
    height: 65px;
    background-size: auto 35px;
    background-repeat: no-repeat;
    background-position: center;

    @media screen and (max-width:$w3) {
      width: 45px;
      height: 45px;
      background-size: auto 25px;
    }
  }

  &_fb {
    background: linear-gradient(180deg, #314AA2 2.18%, #293D69 100.23%);
  }

  &_linkedin {
    background: linear-gradient(180deg, #005983 0%, #003D5A 100%);
  }

  &_telegram {
    background: $blue;
  }

  @each $bg in fb,
  linkedin,
  telegram {
    &_#{$bg}:after {
      background-image: url('../img/#{$bg}.svg');
    }
  }
}

.treeSelector-container,
.treeSelector-container>.treeSelector-wrapper.visible {
  width: 512px;

  @media screen and (max-width:$w3) {
    width: 100%;
  }
}

.treeSelector-container .treeSelector-input-box {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: 0 5px;
  border: none;
  background: #efefef;
}

.treeSelector-container .treeSelector-input-box .title-item {
  margin: 2px 0;
  padding: 0;
  display: flex;
  flex-direction: row-reverse;
  min-height: 24px;
  font-size: 14px;
  background: #C4C4C4;
  align-items: center;
  justify-content: center;
  padding: 2px 8px;
  border-radius: 0;
  border: none;
  margin-right: 10px;
  color: #000000;
  border-radius: 5px;
  transition: $trs;

  &:hover {
    background: $yellow;
  }

  &>.fa {
    padding: 0;
    margin-left: 5px;

    &:before {
      content: '×';
      font-family: Arial;
      font-size: 24px;
      line-height: 100%;
    }
  }
}

.d-none {
  // display: none !important;
}

.toggle-mob-menu-label {
  cursor: pointer;
  position: fixed;
  padding-left: 20px;
  padding-top: 30px;
  z-index: 2;
  display: none;

  @media screen and (max-width:$w3) {
    display: block;
  }

  &>span {
    width: 30px;
    height: 4px;
    background-color: #000;
    display: block;
    margin: 4px 0;
    transition: $trs;
    border-radius: 10px;
    background: $gold;
  }
}

.toggle-mob-menu {
  display: none;

  &:checked {
    &~.toggle-mob-menu-label {
      width: 100%;
      height: 100%;

      &>span {
        background: $silver;

        &:nth-child(1) {
          transform: translateY(8px);
        }

        &:nth-child(3) {
          transform: translateY(-8px);
        }
      }
    }

    &~.mob-menu-list {
      transform: translateX(0);
    }
  }
}

.mob-menu-list {
  position: fixed;
  top: 88px;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 2;
  transform: translateX(-100%);
  transition: $trs;
  display: flex;
  flex-direction: column;
  padding: 20px 30px;
  width: 220px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;

  &>a {
    font-size: 18px;
    margin: 10px 0;
    color: #ffffff;
    transition: $trs;

    &:hover {
      color: $yellow;
    }
  }

  &>.mob-menu-link__phone {
    margin-top: 30px;
  }
}

.alert-window {
  width: 300px;
  position: fixed;
  transform: translate(-50%, -100%);
  top: 50%;
  left: 50%;
  padding: 20px;
  border-radius: 15px;
  border: 2px solid transparent;
  background: #fff;
  opacity: 0;
  z-index: 5;
  display: none;
  transition: $trs;

  &__title {
    margin-bottom: 20px;
  }

  &__text {
    font-size: 20px;
    text-align: center;
  }

  &__success {
    border-color: green;
  }

  &__fail {
    border-color: red;
  }
}

.treeSelector-li {
  position: relative;
}

.treeSelector-li-title {
  padding-left: 30px;
  display: block;

  &:before {
    content: '';
    background: #EFEFEF;
    width: 20px;
    height: 20px;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    line-height: 20px;
    transition: $trs;
    border: 1px solid transparent;

    @media screen and (max-width:$w3) {
      right: calc(100% + 5px);
    }
  }
}

.treeSelector-li-title-box {

  &:hover {
    .treeSelector-li-title:before {
      border-color: $yellow;
      color: $yellow;
    }
  }

  &>input {
    display: none;

    &:checked~.treeSelector-li-title {
      &:before {
        content: '\2713';
      }
    }
  }
}

.treeSelector-container {
  position: relative;

  &:after {
    content: '';
    width: 15px;
    height: 15px;
    border-right: 2px solid #000;
    border-bottom: 2px solid #000;
    position: absolute;
    right: 15px;
    top: 15px;
    transform: translateY(-50%) rotate(45deg);
    pointer-events: none;
    transform-origin: 75% 50%
  }
}

.tooltip {
  padding: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(1, 1, 1, 0.7);
  white-space: nowrap;
  position: relative;
  color: #ffffff;
  font-size: 14px;

  &:after {
    content: '';
    position: absolute;
    left: 100%;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(1, 1, 1, 0.7);
    width: 13px;
    height: 13px;
    clip-path: polygon(0 0, 100% 50%, 0 100%);
  }
}

.golden-text {
  background: linear-gradient(20.9deg, #a27730 0, #ebc55f 26.16%, #fef7bf 53.37%, #e1c065 94.7%);
  -webkit-background-clip: text;
  color: transparent;
}

.contract-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
  transition: transform .5s ease, opacity .5s ease;
  z-index: 6;
  background: #fff;
  border-radius: 22px;
  font-family: 'Roboto', sans-serif;
  display: none;
  opacity: 0;
  font-size: 14px;
  padding: 30px 40px;
  width: 60vw;
  height: 90vh;
  overflow: auto;

  @media screen and (max-width:$w3) {
    border-radius: 10px;
    padding: 10px 20px;
    width: 80vw;
    height: 90vh;
  }
}