* {
	box-sizing: border-box;

	&:after,
	&:before {
		box-sizing: inherit;
	}
}

body,
html {
	height: 100%;
	width: 100%;
	margin: 0;
}

body {
	font-family: 'Source Sans Pro', sans-serif;
	font-size: 18px;
	color: #000;
	font-weight: 400;
	min-width: 320px;
	overflow-x: hidden;

	@media screen and (max-width: $w3) {
		font-size: 16px;
	}
}

h1,
h2,
h3,
h4 {
	margin: 0;
	color: #000;
	font-family: 'Source Sans Pro', sans-serif;
	text-align: center;
}

.first-title {
	font-size: 80px;
}

.second-title {
	font-size: 36px;

	@media screen and (max-width: $w3) {
		font-size: 30px;
	}
}

.third-title {
	font-size: 26px;

	@media screen and (max-width: $w3) {
		font-size: 22px;
	}
}

.fourth-title {
	font-size: 24px;

	@media screen and (max-width: $w3) {
		font-size: 18px;
	}
}

ul,
li {
	margin: 0;
	padding: 0;
	display: block;
}

p {
	margin: 0;
}

img {
	max-width: 100%;
	height: auto;
	display: block;
}

a {
	text-decoration: none;

	&:hover {
		text-decoration: none;
	}
}

header {
	position: relative;
	height: 100vh;
}

section,
footer {
	position: relative;
}

textarea,
button,
input {
	outline: none;
}

textarea {
	resize: none;
}

.container {
	width: 100%;
	margin: 0 auto;
	max-width: 1680px;
	transition: 0.2s ease;
	position: relative;
	z-index: 1;
	padding: 0 20px;

	@media screen and (max-width: $w1) {
		max-width: 100%;
	}
}

.main-button {
	color: #000000;
	font-size: 24px;
	padding: 13px 31px;
	border-radius: 10px;
	border: none;
	cursor: pointer;
	transition: $trs;
	background: $gold;

	&:hover {
		transform: translateY(-3px);
		box-shadow: 0 5px 10px 0 rgba(0, 0, 0, .5);
	}

	&:active {
		transform: translateY(0);
		box-shadow: 0 0 0 0 transparent;
	}

	@media screen and (max-width:$w3) {
		font-size: 20px;

		padding: 10px 25px;
	}
}